.assignments-container {
    display: flex;
    justify-content: left;
    flex-direction: column;
    align-items: flex-start;
    margin-top:2%;
    margin-left: 3%;
}

.assignments-inputs {
    display: flex;
    /* box-sizing: border-box; */
    margin: 0;
    padding: 0;
    flex-direction: column;
    /* align-items: center; */
    width: 90%;
    justify-content: right;
}

.assignment-card {
    margin: 10px;
    padding: 10px;
    background-color: #eee;
    border-radius: 10px;
    cursor: pointer;
}

.left {
    text-align: left;
    width: 180px;
    margin-right: 10px;
}

.assignment-details {
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
    margin-top: 0;
    /* margin-right: 100px; */
    /* margin-left: 10px; */

}

.sub-details{
    margin-top: 0;
    margin-left: 10px;
    /* cursor: pointer; */
    width: 200px;
}

.btn-create {
    float: center;
    background-color: #5281dd;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
}

.btn-create:hover {
    background-color: rgb(22, 61, 58);
}

.editor{
    border: 0.05px solid rgb(130, 126, 126);
}